@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Bricolage+Grotesque:opsz,wght@10..48,200;10..48,300;10..48,400;10..48,500;10..48,600;10..48,700;10..48,800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  /* font-family: "Poppins", sans-serif; */
  font-family: 'Bricolage Grotesque', sans-serif;

}

.navbar {
  /* background: red; */
  height: 80px;
}
.bars{
  display: none;
}
.logo img {
  width: 50%;
  height: 20%;
  /* background: cyan; */
}
.menu {
  width: 30%;
  height: 100%;
}

.menu ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  font-weight: 500;
  /* background: orangered; */
}
.menu-link{
  text-decoration: none;
  color: rgb(95, 95, 95);
  /* color: black; */
  font-weight: 600;
  transition: .6s;
}

.menu-link:hover{
  color: black;
}

.menu-link .active{
  color: red;
}

.btn.btn-success{
  background: orangered !important;
  border: orangered;
}

.hero-text {
  margin-top: 170px;
}

.hero-text h4 {
  font-size: 18px;
}

.hero-text p {
  width: 55%;
  font-size: 18px;
  color: rgb(68, 68, 68);
}

.hero-vid {
  width: 85%;
  height: 230px;
  margin-top: 80px;
  background: url(./Imgs/hero-vid.png) center/cover;
}

.hero-vid img {
  width: 80px;
  background: white;
  border-radius: 50%;
  position: relative;
  top: 80%;
  left: 40%;
  /* transform: translate(-50%, -50%); */
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 25px,
    rgba(0, 0, 0, 0.164) 4px 6px 6px;
  cursor: pointer;
}

.section-right {
  height: 90vh;
  display: flex;
  align-items: flex-end;
  justify-content: space-evenly;
}

.product-box1 {
  width: 270px;
  height: 480px;
  background: radial-gradient(
    farthest-corner at 270px 100px,
    rgba(0, 82, 129, 0.5),
    rgba(5, 71, 109, 0.863),
    rgb(0, 88, 136)
  );
  margin-bottom: 80px;
  box-shadow: rgba(0, 0, 0, 0.19) 4px 10px 25px, rgba(0, 0, 0, 0.23) 4px 6px 6px;
  color: white;
  cursor: pointer;
  transition: 0.5s;
}

.product-box1:hover {
  transform: translateY(-2%);
}

.product-box1 img {
  position: relative;
  right: 80px;
  bottom: 36px;
  width: 130%;
}

.product-box2 {
  color: white;
  width: 280px;
  height: 630px;
  /* background: orangered; */
  margin-bottom: 130px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 25px, rgba(0, 0, 0, 0.23) 4px 6px 6px;
  background: radial-gradient(
    farthest-corner at 280px 300px,
    rgba(20, 124, 113, 1),
    rgba(0, 48, 43, 1)
  );
  cursor: pointer;
  transition: 0.5s;
}

.product-box2:hover {
  transform: translateY(-2%);
}

.product-box2 img {
  position: relative;
  left: 50px;
  top: 19px;
  width: 80%;
}

.product-box1 h2,
h3 {
  font-size: 20px;
  margin-top: 20px;
  margin-left: 20px;
}

.product-box2 h2,
h3 {
  font-size: 20px;
  margin-top: 20px;
  margin-left: 20px;
}

.product-box1 p {
  margin-left: 20px;
  margin-top: 20px;
}

.product-box2 p {
  margin-left: 20px;
  margin-top: 20px;
}

.offer {
  height: 100vh;
  margin-top: 100px;
}

.offer-text {
  /* background: red; */
  margin-top: 30px;
}

.offer-text span {
  color: orangered;
  font-weight: bold;
}

.discount {
  /* background: orangered; */
  width: 40%;
}
.discount h4, .date h4{
  font-size: 20px;
}

.date {
  width: 40%;
}

.offer-img-main {
  border-radius: 20px;
  height: 350px;
  width: 100%;
  object-fit: cover;
  margin-top: 100px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 25px, rgba(0, 0, 0, 0.23) 4px 6px 6px;
}
.thumb-images {
  /* background: red; */
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}

.thumb-images img {
  width: 260px;
  margin-left: 40px;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 25px, rgba(0, 0, 0, 0.23) 4px 6px 6px;
}

.sale-main {
  margin: 140px 70px;
  font-weight: 600;
  /* background: red; */
  width: 100%;
  color: rgb(68, 68, 68);
  /* background: red; */
  width: 90%;
  
}

.sale-main span {
  color: orangered;
}

.homeproducts {
  /* background: red; */
  height: auto;
}
.heading {
  color: orangered;
  font-weight: 700;
  text-transform: uppercase;
}

.heading span {
  color: rgb(0, 88, 136);
}

/* card */

.card {
  width: 350px;
  box-shadow: rgba(0, 0, 0, 0.15) 3px 3px 8px;
}

.card-img-box {
  width: 300px;
  height: 320px;
  /* background: red !important; */
  object-fit: contain;
  margin: auto;
}

.card-img-box img {
  width: 250px;
  height: 320px !important;
  object-fit: contain;
  padding-bottom: 30px;
}

.card-text {
  /* background: cyan; */
  height: 270px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: justify;
}

.main-card {
  /* background: cadetblue; */
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

/* card */

.item-title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  line-clamp: 3;
  -webkit-box-orient: vertical;
}


.see-more{
  width: 100%;
}

.sub-text{
  padding: 80px 0;
  margin-top: 50px;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

.email input{
  width: 300px;
  height: 40px;
  margin-right: 20px;
  border-radius: 12px;
  border: .5px solid black;
  outline: none;
}

.Slider{
  height: 100vh;
}

.about{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  gap: 30px;
  /* background: red; */
  /* flex-direction: row; */
}

.about-flex-change{
  flex-direction: row-reverse !important; 
}

.about-left{
  width: 50%;
}
.about-left h1{
  font-size: 55px;
  font-weight: 700;
  margin-bottom: 30px;
}
.about-left span{
  color: orangered;
}
.about-left p{
  width: 90%;
}
.about-left a{
  font-weight: 600;
}
.about-left a:hover{
  color: orangered;
}
.about-right{
  width: 50%;
}

.contact{
  display: flex;
  height: 90vh;
  justify-content: center;
  align-items: center;
}

.contact-left{
  width: 50%;
  /* background: green; */
}
.contact-left h1{
  font-size: 50px;
  padding-right: 150px;
}
.contact-left p{
  margin: 30px 0px;
}
.mail-box{
  /* background: red; */
  width: 60%;
  display: flex;
  align-items: center;
}
.mail-box p{
  margin: 0;
}
.icon{
  width: 15%;
  font-size: 40px;
  background: gray;
  text-align: center;
  border-radius: 10px;
}

.mail-text{
  margin-left: 20px;
}

.mail-text a{
  color: orangered;
  font-weight: 500;
}
.mail-text a:hover{
  opacity: .6;
}

.contact-right{
  width: 50%;
  /* background: blue; */
}

.contact-form{
  /* background: red; */
  display: flex;
  justify-content: center;
  align-items: top;
  height: 100vh;
}
.contact-form-left{
  width: 50%;
  padding-top: 70px;
}

.contact-form-right{
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}



.contact-form-right form{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* align-items: center; */
  height: 60%;
  width: 70%;
  margin: auto;
  padding: 30px;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );

}
.contact-form-right form h1{
  font-size: 30px;
}

.contact-form-right form input{
  width: 100%;
  height: 40px;
  border-radius: 10px;
  border: none;
  outline: none;
  padding-left: 5px;
  background: rgba(255, 68, 0, 0.103);
}

.send{
  width: 150px;
  height: 40px;
  background: orangered;
  color: white;
}

/* .cart{
  position: absolute;
  right: 20px;
  z-index: 100;

} */

.btn-orange{
  background: orangered;
  color: white;
}


.cart-box{
  /* background: red; */
  display: flex;
  justify-content: space-between;
  align-items: top;
}

.product-name{
  width: 70%;
}

.total-price{
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
  margin-top: 30px;
}








@media only screen and (min-width: 821px) and (max-width: 1280px) {
  .home {
    /* background: red; */
  }

  .section-right {
    justify-content: space-between;
  }
  .product-box1 {
    margin-bottom: 20px;
    width: 250px;
  }
  .product-box2 {
    height: 480px;
    top: 10%;
  }
  .product-box2 img {
    top: 24px;
    width: 70%;
  }
}


@media only screen and (min-width: 500px) and (max-width: 820px) {
  .home{
    background: red;
    flex-direction: column;
  }
  .section-left{
    width: 100% !important;
    background: cyan;
  }

}


@media only screen and (min-width: 300px) and (max-width: 499px) {
  
  *{
    /* overflow-y: none; */
    /* border: 1px solid red; */
  }

  .navbar{
    /* background: cadetblue; */
    width: 100%;
    height: auto;
    display: flex;
    margin-top: 10px;
  }
  .logo{
    width: 30%;
    /* background: green; */
  }
  .logo img{
    width: 100%;
  }
  .menu{
    width: 70%;
    /* background: rebeccapurple; */
  }

  .bars{
    display: block;
  }
  .bars a{
    font-size: 26px;
    color: orangered !important;
    text-decoration: none;
    cursor: pointer;
  }
  .menu{
    /* position: relative; */
    /* top: 20px; */
    /* left: 50%; */
    width: 90%;
    height: 250px;
    margin: auto;
    z-index: 100;
    display: none;
    transition: all 0.5s ease;
  }
  .menu ul{
    flex-direction: column;
    height: 200px;
  }
  .home {
    /* background: red; */
    flex-direction: column;
    overflow: none;
  }
  .section-left {
    width: 100% !important;
  }
  .hero-text{
    width: 90%;
    margin: auto;
    text-align: left;
    margin-top: 30px;
    height: 200px;
    /* background: red; */
  }
  .hero-text p{
    width: 100%;
    text-align: left;
    margin: auto;
  }
  .hero-vid{
    width: 100%;
    margin-top: 30px;
  }
  .section-right{
    /* background: red; */
    width: 100% !important;
    flex-direction: column;
    margin-top: 180px;
    height: auto;
  }
  .product-box2{
    transform: translateX(-30%) !important;
    
  }
  .product-box2 img{
    width: 85%;
    left: 80px;
    top: -8px;
  }

  .offer{
    width: 90%;
    /* background: red; */
    flex-direction: column;
    /* overflow: none !important; */
    margin-top: 0px;
    height: auto;
  }
  .offer-section-left{
    width: 100% !important;
  }
  .offer-text{
    /* justify-content: space-evenly !important; */
    width: 100%;
    gap: 20px;
    flex-direction: column;
  }
  .discount{
    width: 100%;
  }
  .discount h4{
    font-size: 20px;
  }
  .date{
    width: 100%;
  }
  .date h4{
    font-size: 20px;
  }
  .offer #lottie{
    margin-top: 0px;
    padding-top: 0px;
    width: 100% !important;
  }
  .offer-section-right{
    width: 100% !important;
    /* background: red; */
  }
  .thumb-images{
    width: 100%;
    flex-direction: column;
  }
  .thumb-images img{
    margin-left: 0px;
    margin-top: 20px;
  }
  .sale-main{
    text-align: center;
    width: 80%;
    margin: 50px auto;
    /* background: red; */
    font-size: 26px;
  }
  .sub-text{
    width: 100%;
    flex-direction: column;
    text-align: center;
  }
  .email{
    margin-top: 20px;
  }
  .email .btn{
    margin-top: 20px;
  }
  .homeproducts{
    /* background: red; */
  }
  .about{
    flex-direction: column-reverse;
    align-items: center;
    width: 90%;
    height: auto;
  }
  .about-left{
    /* text-align: center; */
    width: 100%;
  }
  .about-left h1{
    font-size: 40px;
  }
  .about-right{
    width: 100%;
    
  }
}
